import React from 'react';
const movies=[{id:1, title:"Spider-Man:Across the Spider-Verse", image:"data:image/jpeg;base64,"},
  {id:2,title:"El Camino: A Breaking Bad Movie", image:"https://m.media-amazon.com/images/M/MV5BYTYxMjI2YzUtODQ5Mi00M2JmLTlmNzItOTlkM2MyM2ExM2RlXkEyXkFqcGc@._V1_FMjpg_UX1000_.jpg"},
  {id:3,title:"The Hateful Eight", image:"data:image/jpeg;base64,"},
  {id:4,title:"The Man From U.N.C.L.E.", image:"data:image/jpeg;base64,"},
  {id:5,title:"Mid90s", image:"data:image/jpeg;base64,"},
  {id:6,title:"Hustle", image:"data:image/jpeg;base64,"},
  {id:7,title:"Parasite", image:"data:image/jpeg;base64,"},
]
function Movies() {
  return (
    <div className="page-content">
      <h3>Movies Page</h3>
      <p>Here you'll find a list of awesome movies!</p>
      <div className='movie-list'>{movies.map(movies => (
        <div key={movies.id} className="movie-thumb">
          <img src={movies.image} alt={movies.title}></img>
        </div>
      ))}</div>
    </div>
  );
}

export default Movies;
