import React from 'react';
const shows = [
  {id:1, title:"Breaking Bad", image: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRKDpXSjbEBTBIPqNaOfiwk_VPfi6I6-AVklqfGlONWmXtxt3uK" },
  {id:2, title:"Better Call Saul", image:'data:image/jpeg;base64,' },
  {id:3, title:"Stranger Things", image:"img/strangerthings.jpg" },
  {id:4, title:"You", image:"img/you.jpg" },
  {id:5, title:"Squid Game", image:'img/squidgame.jpg' },
  {id:6, title:"Wednesday", image:"data:image/jpeg;base64," },
  {id:7, title:"Narcos", image:"data:image/jpeg;base64," },
]
function TVShows() {
  return (
    <div className="page-content">
      <h3>TV Shows Page</h3>
      <p>Check out the latest TV shows available to stream.</p>
      <div className='tv-shows'>{shows.map(shows => (
         <div key={shows.id} className="movie-thumb">
            <img src={shows.image} alt={shows.title} />
          </div>
      ))} </div>
    </div>
  );
}

export default TVShows;
