import React from 'react';

const movies = [
  { id: 1, title: 'Glass Onion:A Knives Out Mystery', image: 'data:image/jpeg;base64,' },
  { id: 2, title: 'Hit Man', image: 'data:image/jpeg;base64,' },
  { id: 3, title: 'Despicable Me 4', image: 'data:image/jpeg;base64,' },
  { id: 4, title: 'Beetlejuice Beetlejuice', image: 'https://m.media-amazon.com/images/I/71YA6AM-V8L.__AC_SX300_SY300_QL70_FMwebp_.jpg' },
];
const top10Movies = [
  { title: "Carry-On", image: "img/carryon.jpg", link: "#" },
  { title: "Bird-Box", image: "img/birdbox.jpg", link: "#" },
  { title: "Don't Look Up", image: "img/dontlookup.jpg", link: "#" },
  { title: "Bullet Train", image: "img/bullettrain.jpg", link: "#" },
  { title: "28 Weeks Later", image: "img/28weekslater.jpg", link: "#" },
  { title: "Rebel Ridge", image: "img/rebelridge.jpg", link: "#" },
  { title: "The Irishman", image: "img/irishman.jpg", link: "#" },
  { title: "All Quiet on the Western Front", image: "img/allquiet.jpg", link: "#" },
  { title: "The Old Guard", image: "img/oldguard.jpg", link: "#" },
  { title: "The Harder They Fall", image: "img/hardertheyfall.jpg", link: "#" },
];
const top10TV = [
  {title:'Squid Games' , image:'img/squidgame.jpg' , link:'#'},
  {title:'Stranger Things' , image:'img/strangerthings.jpg' , link:'#'},
{title:'You' , image:'img/you.jpg' , link:'#'},
{title: 'Outer Banks' , image:'img/outerbanks.jpg', link: '#'},
{title: "Grey's Anatomy" , image:'img/greysanatomy.jpg', link: '#'},
{title: 'Lost' , image:'img/lost.jpg', link: '#'},
{title: 'Mr. Robot' , image:'img/mr-robot.jpg', link: '#'},
{title: 'Mindhunter' , image:'img/mindhunter.jpg', link: '#'},
{title: 'WWE RAW' , image:'img/raw-netflix.jpg', link: '#'},
{title: 'Dahmer Monster: The Jeffrey Dahmer Story' , image:'img/dahmer.jpg', link: '#'},


];


function MovieGrid() {
  return (
    <section className="movies-section">
      <h3>Popular on Netflix</h3>
      <div className="movie-grid">
        {movies.map(movie => (
          <div key={movie.id} className="movie-thumb">
            <img src={movie.image} alt={movie.title} />
          </div>
      
        ))}
      </div>
      <h3>Top 10 Movies in the U.S. Today</h3>
        <div className="movie-grid">
    {top10Movies.map((movies, index) => (
      <div className="movie-container" key={index}>
        <div className="ranking-number">{index + 1}</div>
        <div className="movie-thumb">
          <img src={movies.image} alt={movies.title} />
        </div>
      </div>
    ))}
  </div>
  <h3>Top 10 T.V. Shows in U.S. Today</h3>
  <div className="movie-grid">
    {top10TV.map((movies, index) => (
      <div className="movie-container" key={index}>
        <div className="ranking-number">{index + 1}</div>
        <div className="movie-thumb">
          <img src={movies.image} alt={movies.title} />
        </div>
      </div>
    ))}
  </div>
  
    </section>
  );
}

export default MovieGrid;
